:root {
  --spacing-tiny: 0.5rem;
  --spacing-small: 1rem;
  --spacing-medium: 2rem;
  --spacing-large: 4rem;
  --spacing-huge: 8rem;
  --spacing-gigantic: 16rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

.o-layout {
  display: flex;
  flex-wrap: wrap;
  margin-left: 1rem;
  margin-top: 1rem;
}

.o-layout--align-start {
  align-items: start;
}

.o-layout--align-center {
  align-items: center;
}

.o-layout--align-end {
  align-items: end;
}

.o-layout--justify-start {
  justify-content: start;
}

.o-layout--justify-center {
  justify-content: center;
}

.o-layout--justify-end {
  justify-content: end;
}

.o-layout__item {
  padding-left: 1rem;
  padding-top: 1rem;
}